<template>
  <div>
    <div class="art-lis">
      <!--  -->
      <h2 class="art-title">{{detailData.article_title}}</h2>
      <!--  -->
      <p class="art-content" v-html="detailData.article_content"></p>
      <!--  -->
      <div class="art-icon">
        <span class="heat-icon">
          <i class="iconfont icon-icon" title=""></i>
        </span>
        <span class="article-heat">{{articleHeat}}</span>
        <span class="creat-time">
          <i class="iconfont icon-dingdanxiangqing-chuangjianshijian" title=""></i>
          {{detailData.create_time}}
        </span>
        <span class="update-time">
          <i class="iconfont icon-gengxinshijian" title=""></i>
          {{detailData.update_time}}
        </span>
      </div>
    </div>
    <!--  -->
    <h3 class="text-title"></h3>
    <el-input
      type="textarea"
      :rows="4"
      placeholder=""
      v-model="commentText"
      resize="none"
      class="textacer"
    ></el-input>
    <el-button type="info" @click="textsubmit" class="submitArt" plain></el-button>
    <!--  -->
    <h3 class="counttitle">{{tSlip}}</h3>
    <div class="commentItem" v-for="(item, index) in commentItem" :key="index">
      <h4 class="com-name">{{item.commentUser}}</h4>
      <!--  -->
      <p>{{item.commentContent}}</p>
      <!--  -->
      <!--  -->
      <div class="art-icon">
        <span class="creat-time">
          <i class="iconfont icon-dingdanxiangqing-chuangjianshijian" title=""></i>
          {{item.createTime}}
        </span>
        <span class="update-time">
          <i class="iconfont icon-gengxinshijian" title=""></i>
          {{item.updateTime}}
        </span>
        <span class="reply" @click="reply(index)">{{item.commentVoList.length}} </span>
        <!--  -->
        <!-- itemshow -->
      </div>
      <div class="underline"></div>
      <!--  -->
      <commendchild :datacom="item.commentVoList" v-if="item.show"></commendchild>

      <div class="recomment" v-if="item.show">
        <span class="com-name child-name">{{userName}}</span>
        <el-input
          type="textarea"
          :rows="4"
          placeholder=""
          v-model="childtext"
          resize="none"
          class="childtext"
        ></el-input>
        <!-- id -->
        <el-button class="child-sub" @click="comcom(item.commentId,item.commentArticleId)"></el-button>
      </div>

      
    </div>
  </div>
</template>
<script>
import qs from "qs";
import { ajax } from "../../network/request";
import commendchild from "./child/commend";
export default {
  name: "detailData",
  data() {
    return {
      // detailData
      detailData: null,
      // 
      timerId: "",
      //
      commentText: "",

      //
      commentItem: [],
      childtext: ""
    };
  },
  computed: {
    //
    articleHeat() {
      return this.detailData.article_heat;
    },
    //articleId
    articleId() {
      return this.detailData.article_id;
    },

    userName() {
      return this.$store.state.userName;
    },
    token() {
      return this.$store.state.token;
    },
    //
    tSlip() {
      return this.commentItem.length;
    }
  },
  methods: {
    textsubmit() {
      //
      let data = {
        commentFarther: 0,
        commentContent: this.commentText,
        userName: this.userName,
        commentArticleId: this.articleId
      };
      ajax({
        url: "/comment/insertComment",
        method: "post",
        data: qs.stringify(data),
        headers: { accessToken: this.token }
      }).then(res => {
        if (res.status == 0) {
          //
          this.$message("");
          this.commentText = "";

          //
          let data = {
            articleId: this.articleId,
            userName: this.userName
          };
          ajax({
            url: "/comment/selectComment",
            method: "post",
            data: qs.stringify(data),
            //token
            headers: { accessToken: this.token }
          }).then(res => {
            if (res.status == 0) {
              //
              this.commentItem = res.data;
              // 
            }
          });
        } else if (res.status == 1) {
          this.$message("");
          return;
        }
      });
    },
    // 
    reply(index) {
      //
      // this.$refs.maxheight.style.maxHeight = "300px";
      //,index
      //TODO:
      //data[index].show = true;
      //
      //commentItem
      let isShow = this.commentItem[index].show;
      if (isShow == true) {
        this.commentItem[index].show = false;
      } else if (isShow == false) {
        this.commentItem[index].show = true;
      }
    },
    //
    comcom(cid, aid) {
      let data = {
        commentFarther: cid,
        commentContent: this.childtext,
        userName: this.userName,
        commentArticleId: this.articleId
      };
      ajax({
        url: "/comment/insertComment",
        method: "post",
        data: qs.stringify(data),
        //token
        headers: { accessToken: this.token }
      }).then(res => {
        if (res.status == 0) {
          //
          this.$message("");
          //
          let data = {
            articleId: this.articleId,
            userName: this.userName
          };
          ajax({
            url: "/comment/selectComment",
            method: "post",
            data: qs.stringify(data),
            //token
            // headers: { accessToken: this.token }
          }).then(res => {
            if (res.status == 0) {
              //
              this.commentItem = res.data;
              // 
            }
          });
        } else if (res.status == 1) {
          this.$message("");
          return;
        }
      });
    }
  },
  components: {
    commendchild
  },
  created() {
    this.detailData = this.$route.query;
    // console.log("detaildata" + this.$route.query);
  },
  mounted() {
    //
    let data = {
      articleId: this.articleId,
      userName: this.userName
    };
    ajax({
      url: "/comment/selectComment",
      method: "post",
      data: qs.stringify(data),
      //token
      // headers: { accessToken: this.token }
    }).then(res => {
      if (res.status == 0) {
        //
        this.commentItem = res.data;
        // 
      }
    });
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      //  `vm` 
      //1articleHeat
      vm.timerId = setTimeout(() => {
        let data = {
          articleId: vm.articleId,
          articleHeat: vm.articleHeat++
        };
        ajax({
          url: "/upload/updateArticleHeat",
          method: "post",
          data: qs.stringify(data)
          //token
          //headers: { token: vm.token }
        }).then(res => {
          if (res.status == 0) {
            //
            vm.$message("");
            //
            clearTimeout(vm.timerId);
          }
        });
      }, 10000);
    });
  }
};

//homepage
</script>
<style scoped>
.art-lis {
  margin-left: 20px;
  margin-right: 20px;
  margin-top: 30px;
  /* width: 960px; */
  background-color: #f9f9f9;
  padding: 25px 25px;
}
.art-content {
  color: #404040;
  margin-top: 20px;
  margin-bottom: 20px;
}
.counttitle {
  margin-top: 100px;
}
.commentItem p {
  margin-bottom: 30px;
  max-width: 900px;
  max-height: 85px;
  overflow: hidden;
  text-indent: 1em;
}

.art-title {
  font-family: -apple-system, BlinkMacSystemFont, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Segoe UI", "PingFang SC",
    "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial,
    sans-serif;
  margin-bottom: 5px;
}
.art-icon {
  color: #b4b4b4;
  font-size: 12px;
}
.art-icon i {
  margin-left: 8px;
  font-size: 11px;
}
.heat-icon i {
  color: #ea6f5a;
}
.text-title {
  margin-top: 30px;
  color: #0090ff;
}
.textacer {
  width: 90%;
  margin: 20px auto;
}
.underline {
  height: 1px;
  background-color: #f0f0f0;
  width: 900px;
  box-shadow: 0px -1px 1px 0px #fbfbfb;
}
.submitArt {
  position: relative;
  top: 30px;
  right: 100px;
}
.reply {
  color: #2f93ff;
  margin-left: 20px;
  cursor: pointer;
}
.commentItem {
  margin-top: 40px;
}
.com-name {
  color: #0090ff;
  margin-bottom: 10px;
  font-weight: normal;
}
.recomment {
  width: 780px;
  height: 200px;
  background-color: #f9f9f9;
  border-radius: 4px;
  margin-left: 50px;
  margin-top: 5px;
  position: relative;
}
.childtext {
  width: 600px;
  margin-left: 150px;
  margin-top: 30px;
}
.child-name {
  position: absolute;
  top: 30px;
  left: 30px;
}
.child-sub {
  float: right;
  margin-top: 5px;
  margin-right: 30px;
}
</style>


