<template>
  <div id="app">
    <!--  -->
    <div class="header">
      <div class="icon"></div>
      <h2 class="title"></h2>
      <!-- <router-link to="/homepage" tag="h4"></router-link> -->
      <h4 class="title" id="index" @click="tohomepage"></h4>
      <h4 class="title" id="cover" @click="towrite"></h4>
      <!-- <el-button @click="towrite" class="write"></el-button> -->

      <input type="text" class="search" v-model="searchtext" />
      <a href="javascript:;" class="searchbtn" @click="tosearch">
        <i class="iconfont icon-RectangleCopy"></i>
      </a>

      <div v-if="userName" class="log-reg-wap">
        <span class="comeback" v-if="this.$route.path == '/pcenter'">{{userName}} </span>
        <span class="comeback" @mouseover="menuShow = true" v-else>: {{userName}}</span>
        <!--    @click="topCenter"  -->
        <!-- <span class="login-out" @click="loginOut"></span> -->
        <!-- div   -->
        <div class="showpag"  @mouseout="menuShow = false"></div>
          <ul class="menu" @mouseover="menuShow = true" v-show="menuShow">
          <li class="pcenter" @click="topCenter"></li>
          <li class="plogout" @click="loginOut"></li>
        </ul>
        
        
      </div>
      <div class="log-reg-wap" v-else>
        <div class="regist">
          <router-link to="/regist"></router-link>
        </div>

        <div class="login">
          <router-link to="/login"></router-link>
          <span>/</span>
        </div>
      </div>
    </div>

    <!--  -->

    <router-view />
  </div>
</template>

<script>
//
import { ajax } from "./network/request";
import qs from "qs";

export default {
  name: "App",
  data() {
    return {
      //
      searchtext: "",
      //pageNum
      pageNum: 1,
      menuShow: false,
    };
  },
  computed: {
    userName() {
      return this.$store.state.userName;
      // return 'haha'
      // return "zhangsan"
    },
    token() {
      return this.$store.state.token;
    }
  },

  methods: {

    tohomepage() {
      this.$router.replace("/homepage");
    },
    tocover() {
      // this.$router.replace('/articledetail');
      this.$router.replace("/homepage");
    },
    topCenter() {
      // console.log(this.$route.path);
      this.$router.replace("/pcenter");
      this.menuShow = false;
    },
    //
    tosearch() {
      //vuex
      this.$store.commit("inputChange", this.searchtext);
      //
      //dataajax
      //
      this.$router.replace("/homepage");

      let data = {
        userName: this.userName,
        userInputText: this.searchtext,
        pageNum: this.pageNum
      };
      ajax({
        url: "/upload/searchArticle",
        method: "post",
        data: qs.stringify(data),
        //token
        // headers: { accessToken: this.token }
      })
        .then(res => {
          if (res.status == 0) {
            //searchArticle
            this.$router.replace({
              path: "searchArticle",
              query: res.data
            });
          }
        })
        .catch(err => {
          alert("");
          // console.log(err);
        });
    },
    //
    towrite() {
      if(!this.userName){
        this.$message('')
        return;
      }
      this.$router.replace("/writearticle");
    },
    //
    loginOut(){
      //vuex
      this.$store.commit('clearLogin');
      //localStorage
      localStorage.removeItem('nameobj');
      //
      this.$router.replace('/login');
      this.menuShow = false;
    },
    //menu
    
  },
  mounted() {
    //localStoragevuexvuex
    let nameobj = localStorage.getItem("nameobj");
    if (nameobj) {
      let nameZ = JSON.parse(nameobj);
      this.$store.commit("hasLogin", nameZ);
      this.$message("" + nameZ.userName);
    }
  }
};
</script>

<style scoped>
#app {
  width: 100%;
  min-width: 960px;
  max-width: 1300px;
  margin: 0 auto;
}
.header {
  width: 100%;
  height: 60px;
  padding: 10px 0;
  background-color: #f6f6f6;
}
.icon {
  width: 80px;
  height: 50px;
  background: url(./assets/icon.jpg);
  background-size: 80px 50px;
  background-repeat: no-repeat;
  float: left;
}
.title {
  float: left;
  line-height: 50px;
  margin-left: 20px;
  color: #2f2f2f;
  cursor: pointer;
}

.regist {
  float: right;
  line-height: 40px;
  margin-left: 5px;
  margin-right: 10px;
}
.login {
  float: right;
  line-height: 40px;
}

.search {
  outline: none;
  width: 200px;
  height: 30px;
  border: none;
  border-radius: 15px;
  margin-top: 10px;
  margin-left: 30px;
  padding-left: 10px;
  vertical-align: top;
  padding-left: 15px;
  padding-right: 15px;
  font-size: 12px;
  color: #666c7a;
}
.searchbtn {
  display: inline-block;
  height: 30px;
  width: 30px;
  margin-top: 10px;
  border-radius: 15px;
  background-color: #fff;
  font-weight: bolder;
  text-align: center;
  line-height: 30px;
  transition: all 0.3s;
}
.searchbtn:hover {
  width: 50px;
}

.log-reg-wap {
  float: right;
  line-height: 40px;
  font-size: 12px;
  color: #666c7a;
  position: relative;
}
.showpag{
  position: absolute;
  top: 10px;
  /* z-index: 1; */
  

  width: 130px;
  height: 140px;
  /* background-color: rgba(0, 0, 0, .3) */
}
.menu{
  position:absolute;
  top: 50px;
  left: 17px;
  background-color: rgba(64, 72, 91, .8);
  color: #d8dade;
  width: 90px;
  font-size: 13px;
  text-align: center;
  
}
.menu > li:hover{
  background-color: #343b4e;
  color: #fff;
  cursor: pointer;
  transition: all .4s;

}
.comeback,.login-out{
  /* cursor: pointer; */
  margin-right: 20px;
  position: relative;
  z-index: 2;
  cursor: pointer;
 
}
.login-out{
  margin-left: 10px;
  margin-right: 10px;
}
.login-out:hover{
  color:#0000cc;
  cursor: pointer;
  
}
</style>



