<template>
  <div class="for-psd">
    <!--  -->

    <!--  -->
    <diV class="clearfix">
      <!-- <router-link to="/login/loginname"></router-link>
      <router-link to="/login/loginmail"></router-link>
      <router-view></router-view>-->
      <!--  -->
      <a href="javascript:;" class="login-type" @click="show = true" :class="{on:show}"></a>
      <a
        href="javascript:;"
        class="login-type-eml"
        @click="show = false"
        :class="{on: !show}"
      ></a>
    </diV>

    <div class="noshow" :class="{on:show}">
      <!-- input -->
      <el-row>
        <el-col :span="4">
          <label for="userName"></label>
        </el-col>
        <el-col :span="20">
          <el-input placeholder="" v-model="userName" id="userName" clearable></el-input>
        </el-col>
      </el-row>

      <el-row v-if="nextmsg == ''">
        <el-col :span="8">
          <label for="userPassword">{{resmsg}}</label>
        </el-col>
        <el-col :span="16">
          <el-input placeholder="" v-model="userAnswer" id="userAnswer" clearable></el-input>
        </el-col>
      </el-row>
      <el-row v-if="nextmsg == ''">
        <el-col :span="8">
          <label for="userPassword"></label>
        </el-col>
        <el-col :span="16">
          <el-input placeholder="" v-model="userPassword" id="userPassword" clearable></el-input>
        </el-col>
        <el-col :span="8">
          <label for="userPassword"></label>
        </el-col>
        <el-col :span="16">
          <el-input placeholder="" v-model="sureUserPassward" id="sureUserPassward" clearable></el-input>
        </el-col>
      </el-row>
      <el-button @click="nextstep" class="nextmsg" round>{{nextmsg}}</el-button>
    </div>
    <!--  -->

    <!--  -->
    <div class="noshow" :class="{on: !show}">
      <!-- input -->
      <el-row>
        <el-col :span="4">
          <label for="userEmail"></label>
        </el-col>
        <el-col :span="15">
          <el-input placeholder="" v-model="userEmail" id="userEmail" clearable></el-input>
        </el-col>
        <el-col :span="5">
          <el-button @click.prevent="getcode"></el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="4">
          <label for="passwordNumber"></label>
        </el-col>
        <el-col :span="20">
          <el-input v-model="passwordNumber" id="passwordNumber"></el-input>
        </el-col>
        
      </el-row>
      <!--  -->
      <!--  -->

      <el-row v-if="emailmsg == ''">
        <el-col :span="8">
          <label for="userPassword"></label>
        </el-col>
        <el-col :span="16">
          <el-input placeholder="" v-model="userPassword" id="userPassword" clearable></el-input>
        </el-col>
        <el-col :span="8">
          <label for="userPassword"></label>
        </el-col>
        <el-col :span="16">
          <el-input placeholder="" v-model="sureUserPassward" id="sureUserPassward" clearable></el-input>
        </el-col>
      </el-row>
      <el-button @click="emailmsg" v-if="nextmsg == ''" round>{{emailmsg}}</el-button>

      <!-- <el-row>
        <el-col :span="8">
          <label for="userPassword"></label>
        </el-col>
        <el-col :span="16">
          <el-input placeholder="" v-model="userPassword" id="userPassword" clearable></el-input>
        </el-col>
      </el-row>-->
    </div>
    <!--  -->

    <!-- <a href="javascript:;" @click="forgetpsd"></a> -->
    <!--  -->
    <!-- <router-link @click='changepsd'></router-link> -->
  </div>
</template>
<script>
import { ajax } from "../../network/request";
import qs from "qs";
export default {
  data() {
    return {
      input: "",
      show: true, //truefalse
      userName: "", //
      userEmail: "", //
      //
      nextmsg: "", //     
      userPassword: "", //
      sureUserPassward: "",
      resmsg: "", //
      userAnswer: "", //
      token: "", //token
      passwordNumber: "", //
      emailmsg: "" //emailmsg
    };
  },
  methods: {
    //
    nextstep() {
      //

      //
      if (this.nextmsg == "") {
        let data = {
          userName: this.userName
        };
        ajax({
          url: "/user/getQuestion",
          method: "post",
          data: qs.stringify(data)
        })
          .then(res => {
            // let res = JSON.parse(response);
            //
            console.log("" + res);
            if (res.status == 0) {
              this.nextmsg = "";
              switch (res.msg) {
                case "0":
                  this.resmsg = "";
                  break;
                case "1":
                  this.resmsg = "";
                  break;
                case "2":
                  this.resmsg = "";
                  break;
                case "3":
                  this.resmsg = "";
                  break;
                case "4":
                  this.resmsg = "";
                  break;
              }
            } else if (res.status == 1) {
              //
              this.$message("");
            }
          })
          .catch(err => {
            alert("");
          });
      }
      //
      else if (this.nextmsg == "") {
        let data = {
          userName: this.userName,
          userAnswer: this.userAnswer
        };
        ajax({
          url: "/user/setAnswer",
          method: "post",
          data: qs.stringify(data)
        })
          .then(res => {
            console.log("" + res);
            // let res = JSON.parse(response);
            //
            if (res.status == 0) {
              //
              this.token = res.msg;
              alert("" + res.msg);
              this.nextmsg = "";
            } else if (res.status == 1) {
              //
              alert("msg" + res.msg);
              //this.$message("");
            }
          })
          .catch(err => {
            alert("");
          });
      } else if (this.nextmsg == "") {
        //
        let data = {
          userName: this.userName,
          password: this.userPassword,
          token: this.token
        };
        if (this.userPassword != this.sureUserPassward) {
          // this.$message("")
          this.$message({
            message: ""
          });
          // alert("")
          return;
        }

        //
        ajax({
          url: "/user/setPassword",
          method: "post",
          data: qs.stringify(data)
          // headers: { token: this.token }
        })
          .then(res => {
            console.log("" + res);
            // let res = JSON.parse(response);
            //
            if (res.status == 0) {
              //
              alert("success" + res.msg);
            } else if (res.status == 1) {
              //
              alert("false" + res.msg);
              //this.$message("");
            }
          })
          .catch(err => {
            alert("");
          });
      }
    },
    //
    getcode() {
      let data = {
        userEmail: this.userEmail
      };

      ajax({
        url: "/user/getEmailCheck",
        method: "post",
        data: qs.stringify(data)
      })
        .then(res => {
          if (res.status == 0) {
            this.$message("");
            this.emailmsg = "";
          } else if (res.status == 1) {
            this.$message("");
          }
        })
        .catch(err => {
          alert("");
        });
    },
    //
    emailtext() {
      let data = {
        userEmail: this.userEmail,
        passwordNumber: this.passwordNumber,
        password: this.userPassword
      };
      ajax({
        url: "/user/setPasswordByEmail",
        method: "post",
        data: qs.stringify(data)
      })
        .then(res => {
          if (res.status == 0) {
            this.$message("");
          } else if (res.status == 1) {
            this.$message("");
            this.passwordNumber = "";
          }
        })
        .catch(err => {
          alert("");
        });
    }
  }
};
</script>
<style scoped>
label {
  line-height: 40px;
  text-align-last: justify;
  text-align: justify;
}
.for-psd {
  font-size: 14px;
  width: 400px;
  margin: 150px auto;
}
.login-type {
  float: left;
  /* margin: 5px 5px; */
  margin-left: 120px;
}
.login-type-eml {
  float: right;
  margin-right: 120px;
}
.noshow {
  display: none;
}
.on {
  display: block;
}
.el-row {
  margin-top: 20px;
}
.nextmsg {
  margin: 30px 0 0 80px;
  width: 100px;
}

.login-type {
  float: left;
  /* margin: 5px 5px; */
  margin-left: 120px;
}
.login-type-eml {
  float: right;
  margin-right: 120px;
}
</style>


