        <el-select v-model="ruleForm.userQuestion" placeholder="" class="select-psd">
          <el-option
            v-for="item in questionObj"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <!--  -->
      <el-form-item label="" prop="userAnswer">
        <el-input v-model="ruleForm.userAnswer" autocomplete="off"></el-input>
      </el-form-item>
      <el-button type="primary" @click="submit(ruleForm)" class="regist-btn"></el-button>
      <el-button @click="resetForm"></el-button>

      <!-- <el-button @click="submit" class="regist-btn" round></el-button> -->
    </el-form>

    <!--  -->

    <!-- <el-col>
      <el-row>
        <el-col :span="4">
          <label for="userName"></label>
        </el-col>
        <el-col :span="20">
          <el-input
            placeholder=""
            v-model="userName"
            id="userName"
            ref="onfocus"
            @blur="checkHas"
            clearable
          ></el-input>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="4">
          <label for="userPassword"></label>
        </el-col>
        <el-col :span="20">
          <el-input placeholder="" v-model="userPassword" id="userPassword" show-password></el-input>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="4">
          <label for="surePassword"></label>
        </el-col>
        <el-col :span="20" id="surepsd">
          <el-input placeholder="" v-model="surePassword" id="surePassword" show-password></el-input>
          <span id="errWarn" v-show="iserr"></span>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="4">
          <label for="userEmail"></label>
        </el-col>
        <el-col :span="20">
