        </el-col>
      </el-row>
    </el-form>-->
  </div>
</template>

<script>
// import axios from 'axios'
import { ajax } from "../network/request";
import qs from "qs";

export default {
  name: "HelloWorld",
  data() {
    // 
    // 416 var uPattern = /^[a-zA-Z0-9_-]{4,16}$/;
    var rcheckUser = (rule, value, callback) => {
      //  /^[a-zA-Z].{15}$/
      var regUser = /^[a-zA-Z0-9_-]{4,16}$/;

      if (!value) {
        return callback(new Error(""));
      }
      setTimeout(() => {
        if (regUser.test(value)) {
          callback();
        } else {
          callback(new Error("416"));
        }
      }, 500);
    };
    ////6 var pPattern = /^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/;
    // 1111
    var rcheckPass = (rule, value, callback) => {
      //  /^[a-zA-Z].{15}$/
      var regPass = /^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/;
      // var regPass = /.{3,}/;
      if (!value) {
        return callback(new Error(""));
      }
      setTimeout(() => {
        if (regPass.test(value)) {
          callback();
        } else {
          callback(new Error(""));
        }
      }, 500);
    };
    //
    var rcheckRePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.ruleForm.userPassword) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    //
    var rcheckEmail = (rule, value, callback) => {
      const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
      if (!value) {
        return callback(new Error(""));
      }
      setTimeout(() => {
        if (mailReg.test(value)) {
          callback();
        } else {
          callback(new Error(""));
        }
      }, 100);
    };

    //
    var rcheckPhone = (rule, value, callback) => {
      const phoneReg = /^1[3|4|5|7|8][0-9]{9}$/;
      if (!value) {
        return callback(new Error(""));
      }
      setTimeout(() => {
        // Number.isIntegeres6,
        // +

        if (!Number.isInteger(+value)) {
          callback(new Error(""));
        } else {
          if (phoneReg.test(value)) {
            callback();
          } else {
            callback(new Error(""));
          }
        }
      }, 100);
    };
    //
    var rcheckAnswer = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(""));
      }
      // callback();
      setTimeout(() => {
        callback();
      }, 100);
    };

    //

    return {
      // 
      rules: {
        // 
        userName: [{ validator: rcheckUser, trigger: "blur" }],
        // 
        userPassword: [{ validator: rcheckPass, trigger: "blur" }],
        // 
        surePassword: [{ validator: rcheckRePass, trigger: "blur" }],
        // 
        userEmail: [{ validator: rcheckEmail, trigger: "blur" }],
        // 
        userPhone: [{ validator: rcheckPhone, trigger: "blur" }],
        // 
        userAnswer: [{ validator: rcheckAnswer, trigger: "blur" }]

        // buyerPhone: [{ validator: checkPhone, trigger: "blur" }],
        // buyerEmail: [{ validator: checkEmail, trigger: "blur" }]
        // { min: 3, max: 5, message: " 3  5 ", trigger: "blur" }
      },

      // 

      ruleForm: {
        userName: "",
        userPassword: "",
        surePassword: "",
        userEmail: "",
        userPhone: "",
        userQuestion: "",
        userAnswer: ""
      },

      questionObj: [
        {
          value: "0",
          label: ""
        },
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        },
        {
          value: "4",
          label: ""
        }
      ],
      value: ""
    };
  },
  methods: {
    submit(formName) {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          // console.log(valid)
          // 
          let data = {
            userName: this.ruleForm.userName,
            userPassword: this.ruleForm.userPassword,
            userEmail: this.ruleForm.userEmail,
            userPhone: this.ruleForm.userPhone,
            userQuestion: this.ruleForm.userQuestion,
            userAnswer: this.ruleForm.userAnswer
          };
          // console.log(JSON.parse(JSON.stringify(data)))

          ajax({
            url: "/user/register",

            method: "post",
            data: qs.stringify(data)
          })
            .then(res => {
              if (res.status == 0) {
                //
                this.$message("");
                this.$router.replace("/login");
              }
              // console.log(res);
            })
            .catch(err => {
              console.log(err);
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm() {
      this.$refs.ruleForm.resetFields();
    },
    //

    rsubmit() {
      let data = {
        userName: this.ruleForm.userName,
        userPassword: this.ruleForm.userPassword,
        userEmail: this.ruleForm.userEmail,
        userPhone: this.ruleForm.userPhone,
        userQuestion: this.ruleForm.userQuestion,
        userAnswer: this.ruleForm.userAnswer
      };
      // console.log(JSON.parse(JSON.stringify(data)))

      ajax({
        url: "/user/register",

        method: "post",
        data: qs.stringify(data)
      })
        .then(res => {
          if (res.status == 0) {
            //
            this.$message("");
            this.$router.replace("/login");
          }
          // console.log(res);
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    checkHas() {
      // console.log(this.$refs.onfocus.$el.getElementsByTagName('input')[0])

      //
      // console.log('')
      let data = {
        userName: this.ruleForm.userName
      };
      // console.log(JSON.parse(JSON.stringify(data)))
      ajax({
        url: "/user/checkRegisterUserName",
        method: "post",
        data: qs.stringify(data)
      }).then(res => {
        if (res.status == 1) {
          //
          this.$message("");
          // this.$router.replace('/login');
          this.$refs.onfocus.$el.getElementsByTagName("input")[0].focus();
        }
        // console.log(res);
      });
    }
  },
  computed: {
    //
    iserr() {
      if (this.userPassword === this.surePassword) {
        return false;
      } else {
        return true;
      }
    },
    submitobj() {
      return {
        userName: this.userName,
        userPassword: this.userPassword,
        userEmail: this.userEmail,
        userPhone: this.userPhone,
        userQuestion: this.userQuestion,
        userAnswer: this.userAnswer
      };
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.registc {
  font-size: 14px;
  width: 400px;
  margin: 150px auto;
}
label {
  line-height: 40px;
  text-align: justify;
  text-align-last: justify;
  padding-left: 5px;
}
#surepsd {
  position: relative;
}
#errWarn {
  position: absolute;
  display: inline-block;
  width: 5px;
  height: 5px;
  background-color: red;
  border-radius: 50%;
  box-shadow: 0px 0px 2px red;

  top: 17px;
  left: 190px;
}
.el-row {
  margin: 10px 0;
}
.regist-btn {
  margin-top: 20px;
  margin-left: 100px;
  width: 180px;
}
.select-psd {
  width: 100%;
}
</style>



