<template>
  <div class="clearfix">
    <div class="art-lis" v-for="(art, index) in localData" :key="index">
      <!--  -->
      <h3 class="art-title" @click="getArticle(index)">{{art.article_title}}</h3>

      <!--  -->
      <p class="art-content" v-html="art.article_content"></p>

      <!--  -->
      <div class="art-icon">
        <span class="heat-icon">
          <i class="iconfont icon-icon" title=""></i>
        </span>
        <span class="article-heat">{{art.article_heat}}</span>

        <span class="creat-time">
          <i class="iconfont icon-dingdanxiangqing-chuangjianshijian" title=""></i>
          {{art.create_time}}
        </span>
        <span class="update-time">
          <i class="iconfont icon-gengxinshijian" title=""></i>
          {{art.update_time}}
        </span>
        <el-button class="change-art" @click="changearticle(index)"></el-button>
        <el-button class="delete-art" @click="deletearticle(index)"></el-button>
      </div>
      <div class="underline"></div>
    </div>
    <!--  -->
    
    <el-button @click="toMycomment" class="write"></el-button>
  </div>
</template>
<script>
//ajax
import { ajax } from "../../network/request";
import qs from "qs";
export default {
  name: "pcenter",
  data() {
    return {
      //
      localData: [],
      clickIndex: ""
    };
  },
  watch(){
    
  },
  computed: {
    userName() {
      return this.$store.state.userName;
    },
    token() {
      return this.$store.state.token;
    }
  },
  methods: {
    
    //indexlocaldataindex
    getArticle(index) {
      this.clickIndex = index;
      let id = this.localData[this.clickIndex].article_id;
      let data = {
        articleId: id
      };
      ajax({
        url: "/upload/getArticle",
        method: "post",
        data: qs.stringify(data),
        //token
        headers: { token: this.token }
      })
        .then(res => {
          if (res.status == 0) {
            //articleDetail
            let query = res.data.result;
            this.$router.push({
              path: "/articledetail",
              query: query
            });
          }
        })
        .catch(err => {
          alert("");
          console.log(err);
        });
    },
    //
    changearticle(index) {
      // articleid
      let changeobj = {
        articleId: this.localData[index].article_id,
        articleTitle: this.localData[index].article_title,
        articleContent: this.localData[index].article_content
      };

      //titlecontentdata
      this.$router.push({
        path: "/changearticle",
        query: changeobj
      });
    },
    deletearticle(index) {
      //id
      let articleId = this.localData[index].article_id;
      let data = {
        articleId: articleId,
        userName: this.userName
      };
      ajax({
        url: "/upload/deleteArticle",
        method: "post",
        data: qs.stringify(data),
        //token
        headers: { accessToken: this.token }
      })
        .then(res => {
          if (res.status == 0) {
            //
            this.localData.splice(index,1);
            //
            this.$message('')
            //div



            // this.$router.replace('/pcenter')
          }
        })
        .catch(err => {
          alert("");
          console.log(err);
        });
    },
    toMycomment(){
      this.$router.push('/owncomment')
    }
  },

  mounted() {
    // http://localhost:8080/upload/getOwnArticles     
    let data = {
      userName: this.userName
    };
    ajax({
      url: "/upload/getOwnArticles",
      method: "post",
      data: qs.stringify(data),
      //token
      headers: { accessToken: this.token }
    })
      .then(res => {
        if (res.status == 0) {
          //
          this.localData = res.data;
          // 
        }
      })
      .catch(err => {
        alert("");
        console.log(err);
      });
  }
};
</script>
<style scoped>
.art-lis {
  margin-left: 120px;
  margin-top: 30px;
  width: 730px;
  position: relative;
}
.art-content {
  line-height: 30px;
  color: #999;
  font-size: 13px;
  margin-bottom: 20px;
  height: 30px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.underline {
  height: 1px;
  background-color: #f0f0f0;
  width: 730px;
  box-shadow: 0px -1px 1px 0px #fbfbfb;
}
.art-title {
  cursor: pointer;
  font-family: -apple-system, SF UI Display, Arial, PingFang SC,
    Hiragino Sans GB, Microsoft YaHei, WenQuanYi Micro Hei, sans-serif;
  margin-bottom: 5px;
}
.art-title:hover {
  text-decoration: underline;
}
.art-icon {
  color: #b4b4b4;
  font-size: 12px;
}
.art-icon i {
  margin-left: 8px;
  font-size: 11px;
}
.heat-icon i {
  color: #ea6f5a;
}
.change-art {
  position: absolute;
  right: 100px;
  top: 55px;
}
.delete-art {
  position: absolute;
  right: 20px;
  top: 55px;
}
.write {
  float: right;
  margin-right: 20px;
  margin-top: 10px;
}
</style>


