<template>
  <div class="loginc">
    <!--  -->

    <!--  -->
    <diV class="clearfix">
      <!-- <router-link to="/login/loginname"></router-link>
      <router-link to="/login/loginmail"></router-link>
      <router-view></router-view>-->
      <!--  -->
      <a href="javascript:;" class="login-type" @click="show = true" :class="{on:show}"></a>
      <a href="javascript:;" class="login-type-eml" @click="show = false" :class="{on: !show}"></a>
    </diV>

    <div class="noshow" :class="{on:show}">
      <!-- input -->
      <el-row>
        <el-col :span="4">
          <label for="userName"></label>
        </el-col>
        <el-col :span="20">
          <el-input placeholder="" v-model="userName" id="userName" clearable></el-input>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="4">
          <label for="userPassword"></label>
        </el-col>
        <el-col :span="20">
          <el-input placeholder="" v-model="userPassword" id="userPassword" show-password></el-input>
        </el-col>
      </el-row>
    </div>

    <div class="noshow" :class="{on: !show}">
      <!-- input -->
      <el-row>
        <el-col :span="4">
          <label for="userEmail"></label>
        </el-col>
        <el-col :span="20">
          <el-input placeholder="" v-model="userEmail" id="userEmail" clearable></el-input>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="4">
          <label for="userPassword"></label>
        </el-col>
        <el-col :span="20">
          <el-input placeholder="" v-model="userPassword" id="userPassword" show-password></el-input>
        </el-col>
      </el-row>
    </div>
    <div class="log-fog">
      <el-button @click="login" round></el-button>
      <!--  -->
      <router-link to="/forgetpsd"></router-link>
    </div>

    <!-- <a href="javascript:;" @click="forgetpsd"></a> -->
    <!--  -->
    <!-- <router-link @click='changepsd'></router-link> -->
  </div>
</template>

<script>
import qs from "qs";
import { ajax } from "../network/request";

export default {
  name: "login",
  data() {
    return {
      input: "",
      show: true, //truefalse
      userName: "",
      userEmail: "",

      userPassword: ""
    };
  },
  methods: {
    login() {
      if (this.show) {
        //
        let data = {
          userName: this.userName,
          userPassword: this.userPassword
        };
        ajax({
          url: "/user/login",
          method: "post",
          data: qs.stringify(data)
          // headers:{'token':"aaaaaaaaaa"}
        })
          .then(res => {
            console.log(res);
            // let res = JSON.parse(response);
            if (res.status == 0) {
              //tokenvuex   ?
              this.$store.commit("hasLogin", res.data);
              // alert(",token" + res.data);
              this.$message("" + res.data.userName);
              //localStorage
              let nameobj = JSON.stringify(res.data);//
              localStorage.setItem('nameobj',nameobj);

              //
              this.$router.replace('/homepage');

              //()
            }
          })
          .catch(err => {
            alert("");
            console.log(err);
          });
      } else {
        //
      }
    },
    forgetpsd() {
      this.$router.replace = "/forgetpsd";
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
label {
  line-height: 40px;
  text-align-last: justify;
  text-align: justify;
}
.loginc {
  font-size: 14px;
  width: 400px;
  height: 200px;
  margin: 150px auto;
}
.login-type {
  float: left;
  /* margin: 5px 5px; */
  margin-left: 120px;
}
.login-type-eml {
  float: right;
  margin-right: 120px;
}
.noshow {
  display: none;
}
.on {
  display: block;
}
.log-fog {
  padding-top: 40px;
}
.log-fog button {
  margin-left: 150px;
  margin-right: 40px;
}
.el-row {
  margin-top: 20px;
}
</style>



